html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Oswald";
}

p {
  margin: 0;
  font-size: 1.6rem;
  font-weight: 300;
  text-align: center;
}

.contenedor {
  margin: 0 auto;
  max-width: 120 rem;
  width: 95%;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1 {
  font-size: 4rem;
  margin: 0;
}

h2 {
  font-size: 3.6rem;
  margin: 0;
}

h3 {
  font-size: 3rem;
  margin: 0;
}

h1, h2, h3 {
  text-align: center;
  font-weight: bold;
}

.centrar-texto {
  text-align: center;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 0 5rem;
  align-items: center;
  background-color: #d4d955;
  flex-direction: column;
  margin-bottom: 2rem;
}
.header img {
  width: 100%;
  height: auto;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
  }
  .header img {
    width: 30rem;
    height: auto;
  }
}

nav {
  display: none;
  text-align: center;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  nav {
    display: flex;
    flex-direction: row;
    margin-top: 1.2rem;
  }
}
nav .link {
  font-size: 3rem;
  margin-right: 2rem;
  color: #648c27;
  margin-top: 1rem;
  text-align: center;
}
nav .link:hover {
  font-size: 3.2rem;
  color: #0d0d0d;
}

.hamburguer {
  width: 5rem !important;
  height: 5rem !important;
}
@media (min-width: 768px) {
  .hamburguer {
    visibility: hidden;
  }
}

.mostrar {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

.img-carrito {
  max-height: 5rem;
  padding: 1rem;
}
@media (min-width: 768px) {
  .img-carrito {
    max-width: 10rem;
    padding: 0;
  }
}

.imagen-main {
  display: flex;
  flex-direction: column;
  margin-bottom: 5rem;
}
.imagen-main img {
  width: 100%;
  height: 30rem;
  overflow: hidden;
  object-fit: cover;
  margin-bottom: 5rem;
}
.imagen-main .darken-image {
  filter: brightness(30%);
}
.imagen-main p {
  margin-top: 1.5rem;
}

.button {
  font-size: 2rem;
  margin: 3rem auto 0 auto;
  text-align: center;
  background-color: #648c27;
  color: #F0F2a0;
  padding: 1.5rem;
  border-radius: 1.5rem;
  text-transform: uppercase;
}
.button:hover {
  background-color: #d4d955;
  color: #0d0d0d;
  font-size: 2.2rem;
}

.footer {
  background-color: #0d0d0d;
  padding: 5rem;
  color: #f5f5dc;
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.footer .contenedor-footer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.footer img {
  width: 5rem;
  height: auto;
}
.footer .navegacion-footer a {
  color: #f5f5dc;
}

.contacto-bg {
  background-image: url(../img/imagenes-sitio/5.jpg);
  height: 40rem;
  background-size: cover;
  background-repeat: no-repeat;
  width: 80%;
  margin: 2rem auto 0 auto;
}

.formulario {
  background-color: #f5f5dc;
  margin: -10rem auto 5rem auto;
  width: 70%;
  padding: 3rem;
}
.formulario .campo {
  display: flex;
  margin-bottom: 2rem;
}
.formulario .campo .campo__label {
  flex: 0 0 9rem;
  text-align: right;
  padding-right: 2rem;
  font-size: 1.6rem;
}
.formulario .campo .campo__field {
  flex: 1;
  border: 1px solid var(--gris);
  font-size: 1.6rem;
}
.formulario .campo .campo__field--textarea {
  height: 15rem;
  font-size: 1.4rem;
}

.galeria .galeria-imagenes {
  margin: 2rem 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  gap: 1rem;
}
@media (min-width: 768px) {
  .galeria .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}
@media (min-width: 1200px) {
  .galeria .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 4rem;
  }
}
.galeria .galeria-imagenes li {
  border: 1px solid #0d0d0d;
}

.nuestro-blog {
  margin-top: 3rem;
}
.nuestro-blog h2 {
  margin: 2rem auto 1rem auto;
}
.nuestro-blog p {
  font-size: 1.8rem;
}

.entradas {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entradas {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1200px) {
  .entradas {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}

.entrada {
  display: flex;
  flex-direction: column;
  border: 1px solid #0d0d0d;
  box-shadow: 0.5rem 0.5rem rgba(0, 0, 0, 0.09);
  padding: 1rem;
}
.entrada img {
  width: 100%;
  height: 50rem;
  object-fit: cover;
  object-position: center center;
  margin-bottom: 1rem;
}
.entrada h3 {
  font-size: 2.5rem;
  font-weight: 400;
  margin-bottom: 1rem;
}
.entrada p {
  font-size: 1.6rem;
  font-weight: 300;
  text-align: justify;
}
.entrada .blog-button {
  font-size: 1.6rem;
}

.articulo-blog {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.articulo-blog img {
  max-height: 40rem;
  max-width: min(100%, 100rem);
}
.articulo-blog h2 {
  font-size: 2.8rem;
}
.articulo-blog h4 {
  font-size: 1.6rem;
  text-transform: uppercase;
  text-align: center;
  font-weight: normal;
}
.articulo-blog h4 span {
  font-weight: bold;
}
.articulo-blog p {
  font-size: 1.6rem;
  text-align: justify;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .articulo-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin-bottom: 3rem;
  }
  .articulo-blog img {
    grid-column: 1/2;
    background-repeat: no-repeat;
    background-size: cover;
    width: auto;
    height: 100%;
    margin: 0 auto;
  }
  .articulo-blog .blog-articulo {
    grid-column: 2/3;
  }
  .articulo-blog .blog-articulo h2 {
    font-size: 3.2rem;
  }
  .articulo-blog .blog-articulo h4 span {
    color: #ea785f;
  }
  .articulo-blog .blog-articulo p {
    font-size: 2rem;
    line-height: 1.5;
  }
}

.related {
  display: flex;
  flex-direction: column;
  margin: 3rem auto;
  align-items: center;
  justify-content: space-between;
  margin: 1rem auto;
}
@media (min-width: 768px) {
  .related {
    flex-direction: row;
    width: 70%;
  }
}

.related-producto {
  background-color: #f5f5dc;
  margin-bottom: 2rem;
  width: 100%;
}
@media (min-width: 768px) {
  .related-producto {
    width: 30rem;
    margin-right: 2rem;
  }
}
.related-producto:last-child {
  margin-bottom: 0;
}
.related-producto img {
  width: 90%;
  height: auto;
  margin: 1rem 5%;
}

.related-info {
  height: auto;
  text-align: center;
}
.related-info h3 {
  font-size: 2.8rem;
  margin-bottom: 1rem;
}
.related-info h4 {
  font-size: 2.5rem;
  text-align: center;
}

.button-related {
  font-size: 2.4rem;
  background-color: #648c27;
  color: white;
  padding: 1rem;
  display: block;
}
.button-related:hover {
  cursor: pointer;
  background-color: #ea785f;
  font-size: 3rem;
  transition: 0.7s ease-in-out;
}

.contenedor-articulo {
  background-color: #c8c8c8;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30rem;
  height: auto;
}
.contenedor-articulo .imagen-articulo-tienda {
  width: 100%;
}
.contenedor-articulo .descripcion-articulo {
  font-size: 1.6rem;
  font-weight: 300;
  color: #0d0d0d;
  margin-bottom: 1.5rem;
}
.contenedor-articulo .titulo-articulo {
  font-size: 2.5rem;
  font-weight: 400;
  text-align: center;
  margin-bottom: 1rem;
  color: #0d0d0d;
}
.contenedor-articulo .titulo-articulo:after {
  border: 1px solid #0d0d0d;
}
.contenedor-articulo .precio-tienda {
  font-weight: bold;
  margin-bottom: 1rem;
}
.contenedor-articulo:hover {
  background-color: #9e9a9a;
  transition: background-color 0.5s ease-in;
}

.boton-tienda {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #f5f5dc;
  background-color: #648c27;
  padding: 1rem 0.5rem;
  border-radius: 1rem;
}
.boton-tienda:hover {
  background-color: #d4d955;
  cursor: pointer;
  color: #0d0d0d;
  font-size: 2.2rem;
}

.correcto {
  display: block;
  background-color: #648c27;
  color: white;
  font-size: 2rem;
  font-weight: bold;
  padding: 1rem;
}

.contenedor-admin {
  display: flex;
  justify-content: space-between;
  margin: 5rem auto;
}

.formulario-admin {
  margin: 2rem auto;
}

.fieldset-admin {
  background-color: #F0F2a0;
  padding: 1rem;
}
.fieldset-admin legend {
  font-size: 1.6rem;
}
.fieldset-admin .container {
  display: flex;
  margin-bottom: 1rem;
}
.fieldset-admin .container label {
  font-size: 1.6rem;
  flex: 1;
}
.fieldset-admin .container input {
  font-size: 1.6rem;
  flex: auto;
}
.fieldset-admin .container select {
  flex: auto;
  font-size: 1.6rem;
}
.fieldset-admin .container textarea {
  flex: auto;
  font-size: 1.4rem;
}

.button-send {
  margin: 1rem;
  font-size: 1.6rem;
  padding: 1rem;
  text-align: center;
  text-transform: uppercase;
  border: none;
  border-radius: 1rem;
  background-color: #d4d955;
  color: #0d0d0d;
}
.button-send:hover {
  cursor: pointer;
  transition: 0.3s ease-in;
  background-color: #648c27;
  color: #f5f5dc;
  font-size: 1.8rem;
}

.resultado-admin {
  background-color: #648c27;
  display: block;
  text-align: center;
  padding: 1rem;
  max-width: 60%;
  margin: 0 auto;
  text-transform: uppercase;
  color: white;
  font-weight: bold;
}

.tabla-articulos {
  margin: 0 auto 2rem auto;
}
.tabla-articulos .cabecera th {
  background-color: #0d0d0d;
  color: #f5f5dc;
  font-size: 2rem;
  padding: 1rem 2.5rem;
}
.tabla-articulos tr {
  padding-top: 1rem;
}
.tabla-articulos td {
  background-color: #f5f5dc;
  color: #0d0d0d;
  font-size: 1.6rem;
  padding: 1rem 2.5rem;
}

.boton {
  display: block;
  color: #0d0d0d;
  padding: 1rem;
  border: none;
  width: 10rem;
  text-align: center;
}
.boton:hover {
  color: #f5f5dc;
  cursor: pointer;
}

.rojo {
  background-color: red;
}
.rojo:hover {
  background-color: darkred;
}

.amarillo {
  background-color: #d4d955;
}
.amarillo:hover {
  background-color: #acac05;
}

.formulario-blog legend {
  font-size: 2.2rem;
}
.formulario-blog label {
  font-size: 2.6rem;
  margin-bottom: 1rem;
  margin-right: 1.5rem;
}
.formulario-blog fieldset {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  margin-bottom: 2rem;
  align-items: center;
  background-color: #f5f5dc;
}
@media (min-width: 768px) {
  .formulario-blog fieldset {
    margin-bottom: 1rem;
    flex-direction: row;
    justify-content: space-between;
  }
}
.formulario-blog input {
  font-size: 2.2rem;
  margin-bottom: 1rem;
  flex: 1;
  margin-right: 2rem;
}
.formulario-blog textarea {
  width: 100%;
  min-height: 15rem;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .formulario-blog textarea {
    min-height: 30rem;
  }
}
.formulario-blog select {
  font-size: 2rem;
  margin-bottom: 2rem;
  flex: 1;
  margin: 1rem auto;
}

.error {
  display: block;
  background-color: red;
  color: white;
  padding: 1rem;
  font-size: 2rem;
  width: 100%;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.articulo-tienda {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
@media (min-width: 768px) {
  .articulo-tienda {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 1rem;
  }
}

.articulo-principal {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-height: 50rem;
  margin-bottom: 2rem;
}
.articulo-principal h2 {
  text-transform: capitalize;
  margin: 0.5rem;
}
.articulo-principal .articulo-imagen {
  width: 40rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .articulo-principal {
    flex-direction: row;
    gap: 1rem;
    justify-content: space-around;
  }
}

.informacion-articulo .precio {
  font-size: 4rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
}
.informacion-articulo .descripcion {
  font-size: 2rem;
  margin-bottom: 1.2rem;
  font-weight: 300;
}
.informacion-articulo .formulario-articulo fieldset {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
  background-color: #f5f5dc;
  padding: 2rem;
}
.informacion-articulo .formulario-articulo fieldset label {
  font-size: 2rem;
  flex: 1;
  margin-right: 1rem;
}
.informacion-articulo .formulario-articulo fieldset select {
  font-size: 2rem;
  flex: 2;
}
.informacion-articulo .formulario-articulo .boton-carrito {
  background-color: #e24e2f;
  font-size: 2rem;
  display: block;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  padding: 1rem;
  border-radius: 1rem;
}
.informacion-articulo .formulario-articulo .boton-carrito:hover {
  transition: 0.3s ease-in-out;
  background-color: #FF2E00;
  color: #f5f5dc;
  font-weight: bold;
